
.item {
  margin: 8px;
  border-radius: 8px;
  position: relative;
  background: #FEFEFE;
  height: 48px;
  font-size: 14px;
  border-bottom: #DFE2E8 solid 1px;
  padding-left: 8px;
}
.item span {
    height: 32px;
    width: 32px;
    display: inline-block;
}
.item input {
    float: left;
    padding-left: 8px;
    line-height: 48px;
    background: transparent;
    border: none;
}
.button {
  display: inline-block;
  margin: 8px;
  border-radius: 8px;
  cursor: pointer;
  background: #D7D7DE;
  height: 32px;
  width: 32px;
  line-height: 32px;
}
.right-align {
  position: absolute;
  right: 0;
}
.addItem, .addItem-button {
  border-radius: 8px;
  border: none;
  display: inline-block;
  padding: 8px;
}
.addItem-button {
  margin-left: 8px;
  background: rgba(0, 0, 0, 0.2);
  color: #ffffff;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  -webkit-transition: all ease .4s;
  transition: all ease .4s;
}
.addItem-button:hover {
    -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
            box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}



