
#app {
  font-family: 'Comfortaa', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
  overflow: hidden;
}
#app.daily {
    background-color: rgba(0, 0, 0, 0.05);
}
#app.weekly {
    background-color: rgba(0, 0, 0, 0.1);
}
#app.monthly {
    background-color: rgba(0, 0, 0, 0.15);
}
#nav-menu {
  -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.15);
  margin-bottom: 16px;
}
#nav-menu > div {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
#nav-menu a {
    height: calc((100vh - 3.778rem) / 3);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100vw;
    color: #fff;
    font-weight: bold;
    font-size: 2rem;
    text-decoration: none;
    -webkit-transition: all linear 0.2s;
    transition: all linear 0.2s;
    background: #DFE2E8;
}
#nav-menu a:nth-child(1) {
      background-color: rgba(0, 0, 0, 0.05);
}
#nav-menu a:nth-child(2) {
      background-color: rgba(0, 0, 0, 0.1);
}
#nav-menu a:nth-child(3) {
      background-color: rgba(0, 0, 0, 0.15);
}
#app.category #nav-menu a {
  width: calc(100vw / 3);
  height: 80px;
  font-size: 1.5rem;
  position: relative;
}
#app.category #nav-menu a.router-link-exact-active:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 100%;
    height: 2px;
    background: #96989F;
}



